.infoBox {
    height: fit-content;
    width: 375px;
    background: rgba(41, 41, 41, 0.971);
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 20px;
    padding: 25px;
    color: white;
    display: flex;
    flex-direction: column;

    .go-to-landing {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
    }
    
    img.spores-logo-black {
        width: 30px;
        height: 30px;
    }

    p {
        margin-top: 10px;
    }

    .join-waitlist-container {
        margin-top: 10px;
        margin-left: auto;
        margin-right: auto;
    }
}

.infoContainer {
    display: flex;
    background-color: #0000006b;
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 1000000;
}

.buyLink {
    text-decoration: underline;
    font-weight: 600;
    color: #61fc9f;
}

.buyLink:hover {
    color: adjust-hue($color: #61fc9f, $degrees: 45);
}

.timer {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    z-index: 1;
}

.mintButton {
    color: white;
    background-color: black;
    margin-top: 40px;
    padding: 15px;
    margin-left: 15px;
    margin-right: 15px;
    border-radius: 5px;
    font-weight: 700;
    text-align: center;
}